* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: 'PT Sans', sans-serif;
}


.home {
  display: flex;
  height: 90vh;
  align-items: center;
  justify-content: center;
  font-size: 2rem;
  background-image: url('./AI.jpg');
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  color: #fff;
  font-size: 70px;
  font-weight: 900;
}


hr.class-1 {
  border-top: 10px solid rgb(13, 0, 253)
}

hr.class-2 {
  border-top: 3px double rgb(0, 68, 253)
}

hr.class-3 {
  border-top: 1px dashed rgb(0, 68, 253)
}

hr.class-4 {
  border-top: 1px dotted rgb(0, 68, 253)
}

hr.class-5 {
  background-color: #fff;
  border-top: 2px dashed rgb(0, 68, 253)
}

hr.class-6 {
  background-color: #fff;
  border-top: 5px dotted rgb(0, 68, 253)
}

